import React, { Component } from 'react';
import { Link } from "react-router-dom";

import styles from './Hackaton.module.scss';
import { Button } from '@material-ui/core';
class Hackaton extends Component {
    render() {
        return (
            <div className={styles.wrapper}>
              <div className={styles.content}>
                  <h2>Hackathon - 13-14 APRILIE 2019</h2>
                  <br></br>
                  <p>Hackathonul reprezint cel de-al doilea eveniment conex din cadrul proiectului FII Practic. 
                    Conceptul este unul ct se poate de incitant, fiind vorba de o competiie.
                    <br/>
                    Participanii acestui concurs marca FII Practic vor fi organizai n echipe, iar n weekendul 13-14 aprilie
                    ei vor avea la dispoziie 24 de ore n care vor trebui s creeze o aplicaie menit s rspund unor nevoi sau probleme ale 
                    comunitii.
                  </p>
                  <Link to='/hackathon'><Button>Afla mai multe</Button></Link>
              </div>
            </div>
        )
    }
};

export default Hackaton;
