import React from 'react';

import styles from './PresentationalHackathon.module.scss';
import Footer from '../Footer/Footer';

const PresentationalHackathon = (props) => {
    return (
        <>
        <div className={styles.wrapper}>
            <div className={styles.title}><h2>Hackathon</h2></div>
            <div className={styles.description}>
                <p>La fel ca n ediiile anterioare, Hackathonul va ncheia evenimentul FII Practic, participanii putnd pune
                   n aplicare, n cadrul acestuia, cunotinele dobndite la traingurile proiectului. Evenimentul este o 
                   competiie n care, pe parcursul a 24 de ore, cei nscrii vor da ce au mai bun i se vor folosi de 
                   cunotinele n domeniul web i mobile pentru a rezolva problema dat.
                </p>
                <p>
                    Ca mod de desfurare, vor exista maximum 10 echipe participante, formate din 2-4 membri. 
                    Aceste echipe vor primi drept sarcin realizarea unei aplicaii menite s rezolve o nevoie
                    sau o problem a comunitii.
                </p>
                <p>
                    Locurile pentru participare vor fi limitate, aa c v ncurajm urmrii startul nscrierilor! 
                    V ateptm n numr ct mai mare!
                </p>

                <i>Inscrierile inca nu au inceput. Urmareste pagina pentru a fi la curent cu toate detaliile.</i>
            </div>
        </div>
        <Footer />
        </>
    )

};

export default PresentationalHackathon;