import React, { Component } from 'react';
import { Link } from "react-router-dom";

import styles from './Conference.module.scss';
import { Button } from '@material-ui/core';
class Conference extends Component {
    render() {
        return (
            <div className={styles.wrapper}>
              <div className={styles.content}>
                  <h2>Conference - 23 FEBRUARIE 2019</h2>
                  <p>FII Practic Conference este primul din cele dou evenimente conexe ale proiectului FII Practic.
                     Acesta cuprinde mai multe prezentri susinute de reprezentani ai companiilor partenere, prezentri ce vor avea loc smbt, 23 februarie.
                    <br/>
                    Att elevii i studenii, ct i cei pasionai de informatic sunt invitai s descopere noi lucruri ce in de domeniul IT sau de ariile adiacente acestuia,
                    precum cea de soft-skills.
                  </p>
                  <Link to='/conference' ><Button>Afla mai multe</Button></Link>
                  
              </div>
            </div>
        )
    }
};

export default Conference;
