import React, { Component } from 'react';

import styles from './About.module.scss';
import Footer from '../../core/components/Footer/Footer';

class About extends Component {
    render() {
        return (
            <div className={styles.wrapper}>
              <div className={styles.content}>
                  <p>
                    Am nceput s fim practici din anul 2012, cnd tefan Coro a venit cu ideea unui proiect care s aduc mai aproape mediul universitar, 
                    cel preuniversitar i cel profesionist. Lui i s-au alturat Andrei Stoleru, Adrian Trefa i Andrei Diaconu. 
                    Sprijinii de Asociaia Studenilor Informaticieni Ieeni i Facultatea de Informatic Iai, cei patru au pus la cale cte un training 
                    pentru fiecare dintre ariile: design grafic, dezvoltarea aplicaiilor Android, project management i aplicaii Web.
                  </p>
                  <p>
                    FII Practic a crescut de la an la an, nu numai prin numrul aplicanilor i participanilor, ct i prin numrul ariilor propuse.
                    n fiecare an alegem ariile n funcie de cerinele comunitii IT din Iai, pentru c ne dorim ca ce nvai aici s fie 
                    relevant pentru viitoarea voastr carier. Aplicanii trebuie sa completeze un formular online pentru fiecare dintre ariile 
                    la care i doresc s participe. Cu toate c unele arii i cer anumite cunotine precedente, pentru cele mai multe domenii 
                    este important atitudinea i dorina de te dezvolta.
                  </p>
                  <ul>
                      <li>Frontend</li>
                      <li>Backend</li>
                      <li>AR/VR</li>
                      <li>Design</li>
                      <li>SoftSkills</li>
                      <li>GameDev</li>
                      <li>3D Modelling</li>
                      <li>Testing</li>
                  </ul>
                  <p>
                    Cele 4 sptmni de traininguri de la Iai se vor desfura n weekend-urile din perioada 9 martie 2019  31 martie 2019 n incinta
                    Facultii de Informatic (Corpul C al Universitaii Alexandru Ioan Cuza).
                  </p>
                  <p>
                    O parte important a experienei voastre la FII Practic este proiectul pe care il veti dezvolta pe parcursul proiectului, o prim reuit n domeniul ales. 
                    Totodat, proiectul este un bun moment pentru a v crete vizibilitatea n comunitate. Fiecare participant va primi o diplom, iar creatorii celor mai bune proiecte se vor bucura i de alte surprize.
                  </p>
                  <p>
                    De asemenea, vor avea loc i dou evenimente conexe: FII Practic Conference i FII Practic Hackathon, care se vor concentra tot pe dezvoltarea in domeniul IT.
                  </p>
                  <p>
                    FII Practic Conference const n 5 sesiuni de prezentri, pe parcursul unei singure zile, dar cel mai important este ca veti avea ocazia sa interactionati cu cei care vor pregati trainingurile la care veti participa.
                  </p>
                  <p>
                    FII Practic Hackathon, aflat la a 5-a ediie, se va desfura pe parcursul a 24 de ore, unde participanii vor putea lucra n echipe, pentru a realiza aplicaii care s rezolve o nevoie sau o problem a comunitii.
                  </p>
                  <p>
                    Calitatea traingurilor pe care le desfurm nu ar fi la fel de nalt dac nu am ncuraja un mediu amical de nvare i 
                    dezvoltare pe parcursul trainingurilor, ct i dup terminarea acestora. Scopul proiectului este s dezvolte, nu s evalueze i,
                    cu toate c vei ntlni oameni cu cariere importante n IT, atitudinea mentorilor va fi una prietenoas.
                  </p>
              </div>
              <Footer />
            </div>
        )
    }
};

export default About;
